      components: {
          'app-header': Header,
          'app-nav': Nav
      },
      data(){
          return {

          }
      },
      computed: {
          showBack(){
              if(this.$route.path === '/index'){
                  return false;
              }else{
                  return true;
              }
          },
          showNav(){
              if(this.$route.path === '/index'){
                  return false;
              }else{
                  return true;
              }
          }
      }
  }

</script>

